@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');

html, body {
  background-color: #f7f7f7;
}
.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;
  display: flex;
  flex-direction: column;
  animation: fadeIn 1s ease-in;
}

@keyframes fadeIn {
  0% {opacity: 0;}
  100% {opacity: 1;}
}
